.section {

  &__examples {
    padding: 50px 0;
  }
  
}

.examples {
  display: flex;
  justify-content: space-between;
  gap: 20px;

  @include mini-desktop {
    flex-wrap: wrap;
    
  }

  &__item {
    flex: 0 1 340px;
    display: flex;
    align-items: center;
    gap: 30px;

    @include mini-desktop {
     flex: 0 1 100%;
      
    }
  }

  &__name {
    font-weight: 700;
    font-size: 14px;
    line-height: 22px;
    color: #000;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: #6a6a6a;
  }

  &__link {
    font-weight: 700;
    font-size: 12px;
    line-height: 22px;
    color: #6a6a6a;
    transition: 0.3s color;
    cursor: pointer;

    &:hover {
      color: red;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}
