@import "../mixins/breakpoint";


.section {
  &__products {
    padding: 125px 0;

    @include mini-desktop {
      padding: 75px 0;
    }

    @include mobile {
      padding: 30px 0;
    }
  }
}

.products {
  display: flex;
  gap: 114px;
  @include mini-desktop {
    display: flex;
    flex-wrap: wrap;
    
  }

  @include mobile {
    gap: 35px;
    
  }

  &__item {
    &.left__item {
      flex: 0 1 230px;
      @include mini-desktop {
        flex: 0 1 100%;
      }
    }

    &.right__item {
      @include mini-desktop {
        flex: 0 1 100%;
      }
    }

 
  }
}

.gallery {
  display: flex;
  gap: 79px;
  

  
    @include mini-desktop {
      justify-content: space-between;
    }

    @include mobile {
      flex: 0 1 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
    }

    &__item {
     
      @include mobile {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px solid #3b3b3b;
        border-radius: 30px;
        padding: 30px;
      }
    }




  &__img {
   img {
    height: 260px;  
    @include mobile{
     
      img {
        max-width: 100%;
        width: 100%;
        height: auto;
      }
    }
   }
  }

  &__info {
    margin-top: 46px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include mini-desktop {
      margin-top: 20px;
    }
  }

  &__name {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #2f2f2f;
  }

  &__price {
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: #2f2f2f;
    
  }


}




