.section {
    &__blog {
        padding-bottom: 150px;
    }
}

.blog {
    display: flex;
    justify-content: space-between;
    margin-top: 36px;
    gap: 20px;
    @include mobile{
        flex-wrap: wrap;
    }
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__link {
        font-weight: 600;
        font-size: 16px;
        line-height: 18px;
        border-bottom: 3px solid #000;
        color: #2f2f2f;
        transition: 0.3s border, color;

        @include mobile{
            border-bottom: 2px solid #000; 
            font-size: 14px;
        }

        &:hover {
            border-color: #385d50;
            color: #385d50;
        }
    }
    
    &__item {
      flex: 0 1 360px;
      @include mobile{
      flex: 0 1 100%;
    }

    }

    &__img {
        border-radius: 10px;
        overflow: hidden;

        img {
            max-width: 100%;
            width: 100%;
            height: auto;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        margin-top: 44px;
        color: #2f2f2f;

        @include mini-desktop{
            margin-top: 20px;
            font-size: 16px;
            margin-bottom: 10px;
        }

       
    }

    &__text {
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: #2f2f2f;
        span {
            font-weight: 400;
            font-style: italic;
        }
    }
}