.section {
	&__hero {
		padding-top: 90px;
		background: #3d5b50;
		position: relative;
		overflow: hidden;
		@include mini-desktop {
			padding-top: 75px;
		}
		@include tablet {
			padding-top: 60px;
		}
		.hero__svg {
			position: absolute;
			right: 120px;
			top: 100px;
			z-index: 0;

			@include mini-desktop {
				top: 20%
			}

			@include mobile {
				top: 45%;
				right: 5%;
				max-width: 150px;
				width: 100%;
				height: auto;

			}

			@include mini-mobile {
				top: -10%;
				right: -2%;
			}

		}
	}
}

.hero {
	display: flex;
	justify-content: space-between;
	@include mini-desktop {
		flex-wrap: wrap;
	}

	&__item {
		img {
			width: 100%;
		}
		z-index: 2;
		&.left__item {
			flex: 0 1 430px;

			@include mini-desktop {
				flex: 0 1 100%;
			}
		}
	}

	&__title {
		font-weight: 600;
		font-size: 54px;
		line-height: 65px;
		color: #fff;
		margin: 0;
		span {
			display: block;
		}

		@include tablet {
			font-size: 40px;
			line-height: 1.3;
		}
		@include mini-desktop {
			font-size: 42px;
		}
		@include mobile {
			font-size: 36px;
		}
	}

	&__btns {
		display: flex;
		margin-top: 117px;
		gap: 14px;
		align-items: center;
		@include mini-desktop {
			margin-top: 75px;
		}
		@include mini-mobile {
			flex-wrap: wrap;
			margin-top: 50px;
			.hero__btn {
				display: flex;
				width: 100%;
				justify-content: center;
			}
		}
	}
}
