.nav {
  &__list {
    display: flex;
    align-items: center;
   

    @include mobile {
      flex-direction: column;
    }
  }

  &__item:not(:last-child) {
    &:not(:last-child){
      margin-right: 30px;
      @include mobile {
        
        margin-bottom: 30px;
        margin-right: 0;
        
        
      }
    }
   
  }

  &__link {
    display: inline-block;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: var(--light-color);
    opacity: 0.5;
    text-transform: capitalize;
    position: relative;
    @include mobile {
      opacity: 1;
    }
    
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -10px;
      background: yellow;
      height: 5px;
      width: 100%;
      transform: scale(0);
      transition: 0.8s transform;

      

    }
   
    &:hover {
      opacity: 1;
      &:after {
        transform: scale(1);
      }
    }
  }
}


