.section {
  &__choose {
    padding-top: 25px;
    padding-bottom: 70px;
    @include mobile {
     padding-bottom: 20px;
    }
  }
}


.choose {
  display: flex;
  justify-content: space-between;
  gap: 20px;

  @include tablet {
   flex-wrap: wrap;
  }
  &__item {
    &.left {
      &__item {
      flex: 0 1 470px;
      padding-top: 60px;
      @include tablet {
        flex: 0 1 100%;
        padding-top: 0px;
      }
      }

     
    }

    &.right {
     &__item {
      padding-top: 50px;
      position: relative;
      @include mini-desktop {
       padding-top: 30px;
       display: flex;
       justify-content: center;
       align-items: center;
      }
      

      &:after {
        position: absolute;
        content: '';
        width: 296px;
        height: 307px;
        border-radius: 20px;
        background: rgba(53, 115, 110, 0.1);
        left: -310px;
        top: 170px;

        @include mini-desktop {
         left: unset;
         top: unset;
         right: -30%;
         bottom: 10%;
         z-index: 1;
         background: rgba(53, 115, 110, 0.3);
        }
        @include mobile {
         width: 180px;
         height: 200px;
         right: -20%;
        }

        @include mini-mobile {
          width: 180px;
          height: 200px;
          right: -4%;
         }
      }

      .why__svg {
        position: absolute;
        left: -80px;
        top: -10px;
        // z-index: -1;
       
      }
      .why__image {
        max-width: 100%;
        width: 100%;
        height: auto;
        display: block;
      }
      @include tablet {
        padding-top: 30px;
      }
     }
    }  
  }
}

.why {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  margin-top: 80px;
  @include middle-desktop { 
     gap: 30px;
  }

  @include tablet {
   margin-top: 35px;
  }
  &__item {
    flex: 0 1 215px;

    @include tablet {
      flex: 0 1 48%;
     
    }

    @include mobile {
      flex: 0 1 100%;
      
    }

    &-title {
      font-weight: 600;
      font-size: 14px;
      line-height: 27px;
      color: #2f2f2f;
      margin-top: 3px;
    }

    &-text {
      font-weight: 400;
      font-size: 14px;
      line-height: 22px;
      color: #2f2f2f;
      margin-top: 9px;
    }
  }
}
