.btn {
  display:  inline-block;
  border: none;
  padding: 11px 35px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 500;
  line-height: 28px;
  letter-spacing: 0.5px;
  cursor: pointer;
  border-radius: 30px;

  &__hero {
    background-color: var(--yellow-color);
    color: #2f2f2f;
    transition: 0.3s background, color;

    &:hover {
      background: lightcoral;
      color: #fff;
    }
  }

  &__outlined {
    border: 2px solid rgba(255, 255, 255, 0.29);
    color: var(--light-color);
    padding: 9px 35px;
    background: transparent;
    transition: 0.3s background, color;

    &:hover {
      background: #fff;
      color: #2f2f2f;
      border-color: #fff;
    }
  }

  

  &__section {
    background-color: var(--green-color);
    color: var(--light-color);
    transition: 0.3s background, color;
    margin-top: 37px;

    @include mobile {
      margin-top: 24px;
      display: flex;
      justify-content: center;
      width: 100%;
      
    }

    &:hover {
      background: lightcoral;
      color: #2f2f2f;
      border-color: #fff;
    }
  }

}
