.loader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 5;
  display: flex;

  &__title {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 38px;
    z-index: 6;
    color: #3b5d50;

    span {
      color: #323232;
    }
  }

  &__item {
    flex: 1 1 auto;
    z-index: 7;
    background: #3b5d50;
  }

  &__item-1 {
    border-right: 1px solid #323232;
  }
  &__item-2 {
    border-right: 1px solid #323232;
  }
  &__item-3 {
     border-right: 1px solid #323232;
  }
}


.header {
  padding-top: 24px;
  padding-bottom: 21px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.29);
  &__container {

    @include tablet {
      display: flex;
      justify-content: space-around;
    }

    @include mobile {
      @include flex-all-sb;
    }
    @include flex-all-sb;
  }

  &--main {
    background-color: var(--green-color);
    
  }

  &__nav {
    //  margin-left: auto;
   

    // @include minidesk {
    //   margin-left: 0;
    // }
     @include mobile {
      position: fixed;
      left: 0;
      top: 0;
      z-index: 1000;
      overflow-y: auto;
      padding: 50px;
      width: 100%;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: var(--green-color);
      transform: translateX(-150%);
      transition: transform 0.3s ease-in-out;

      &--active {
        transform: none;
        opacity: 1;
      }

      
    }

    @include mobile {
      padding: 20px;
       
      
    }
  }

 

  &--secondary {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }
}


.logo {
  &:focus {
    outline-offset: 2px;
     outline: 2px solid var(--color-primary);
    }

    // @include minidesk {
    //   max-width: 100px;
    // }

    @include tablet {
      max-width: none;
     }

     
}





