/* stylelint-disable */
@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}


@mixin middle-desktop {
  @media (max-width: (1290px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin mini-desktop {
  @media (max-width: (992px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mini-mobile {
  @media (max-width: (480px)) {
    @content;
  }
}
