section {
    &__testimonials {
        padding: 45px 0;
    }
}

.swiper  {

    &__testimonials {
      max-width: 760px;
      width: 100%;
      margin: 0 auto;
      overflow: hidden;
      margin-top: 45px;
      
      @include mini-desktop{
       margin-top: 30px;
      }
    }
  
}

.swiper-wrapper {
    padding-bottom: 40px;
}

.testimonials {

    &__slide {
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    &__text {
        font-weight: 400;
        font-size: 18px;
        line-height: 32px;
        text-align: center;
        color: #000;

        @include mini-desktop{
          font-size: 16px;  
          line-height: 1.7;
        }
    }

    &__name {
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        color: #000;
        text-align: center;
        margin-top: 13px;

        @include mini-desktop{
            margin-top: 10px;
        }
    }

    &__position {
        margin-top: 4px;
        font-weight: 400;
        font-size: 13px;
        line-height: 32px;
        color: #2f2f2f;
        text-align: center;
    }

    &__avatar {
        border-radius: 100%;
        overflow: hidden;
        margin-top: 42px;

        @include mini-desktop{
            margin-top: 20px;
        }
    }
}

.swiper-pagination {
    .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
        background: rgba(196, 196, 196, 1);

        &.swiper-pagination-bullet-active {
            background: #385d50;
        }
    }
}

.slider {

    &__wrapper {
        position: relative;
    }
    &__btn {
        width: 58px;
        height: 58px;
        border-radius: 100%;
        border: none;
        position: absolute;
        transition: 0.3s all;
        @include mini-mobile {
            display: none;
        }
        .slider__btn-path {
            stroke: #fff;
        }
       
        background: #385d50;
        &.swiper-button-disabled {
            background: rgba(196, 196, 196, 0.1);
            .slider__btn-path {
                stroke: #000;
            }
        }
   
        &.prev {

            &__btn {
                left: 0;
                top: 30px;
                @include mini-desktop{
                 top: unset;
                 bottom: -29px;
                }
            }

        }

        &.next {

            &__btn {
                right: 0;
                top: 30px;
                @include mini-desktop {
                    top: unset;
                    bottom: -29px;
                }
            }

            

        }
    }
}

