footer {
	background: #fff;
	padding-bottom: 32px;
}

.footer {
	display: flex;
	gap: 60px;
	@include mini-desktop {
		gap: 30px;
	}

	@include mobile {
		flex-wrap: wrap;
	}

	&__text {
		font-weight: 400;
		font-size: 14px;
		line-height: 24px;
		color: #6a6a6a;
	}

	&__logo {
		display: flex;
		margin-bottom: 17px;
	}

	&__item {
		margin-top: 17px;

		&.left__item {
			flex: 0 1 360px;
			@include mobile {
				flex: 0 1 100%;
			}
		}

		&.right__item {
			display: flex;
			gap: 60px;

			@include mini-desktop {
				gap: 30px;
			}
			@include mobile {
				flex: 0 1 100%;
			}

			@include mini-mobile {
				flex-wrap: wrap;
				gap: 10px;
			}
		}
	}

	.social {
		display: flex;
		gap: 11px;
		margin-top: 60px;
		@include mobile {
			margin-top: 20px;
		}

		&__item {
			width: 40px;
			height: 40px;
			border-radius: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: #d9d9d9;
			transition: 0.3s background;

			cursor: pointer;
			svg {
				transition: 0.3s scale;
			}

			&:hover {
				background: #385d50;
				svg {
					scale: 1.1;
					transition: 0.3s fill;
				}

				path {
					fill: #fff;
				}
			}
		}
	}

	.links {
		@include mobile {
			flex: 0 1 20%;
		}
		@include mini-mobile {
			flex: 0 1 45%;
		}
		li {
			line-height: 28px;
			cursor: pointer;
			transition: 0.3s color;

			&:hover {
				color: red;
			}
		}
	}
}

.copy {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 32px;
	padding-top: 36px;
	border-top: 1px solid #eff2f1;
	@include mobile {
		flex-wrap: wrap;
		gap: 20px;
		margin-top: 20px;
		
	}

	&__link {
		transition: 0.3s color;
		&:hover {
			color: red;
		}
	}

	&__item {
		display: flex;
		align-items: center;
		gap: 42px;
		
	}
	&__text {
		@include mini-mobile {
			display: block;
		}
	}
}
