.section {
	&__subscribe {
		background-color: #fff;
		padding: 100px 0 130px 0;
		@include mobile {
			padding: 150px 0 75px 0;
		}
		
	}
}

.subscribe {
	position: relative;

	.subscribe__image {
		position: absolute;
		right: 0;
		top: -180px;

		@include tablet {
		  max-width: 300px;
		}

		@include mobile {
			top: -250px;
		}
	}
	
	&__title {
		display: flex;
		align-items: center;
		gap: 12px;

		span {
			font-weight: 600;
			font-size: 18px;
			line-height: 22px;
			color: #2f2f2f;
		}
	}
}

.form {
	max-width: 600px;
	display: flex;
	justify-content: space-between;
	gap: 15px;
	margin-top: 24px;
	@include mobile {
		max-width: 100%;
		flex-wrap: wrap;
	}

	&__input {
		padding: 14px 22px;
		border-radius: 10px;
		border: 1px solid #c4c4c4;
		display: flex;
		width: 100%;
		font-weight: 400;
		font-size: 14px;
        line-height: 17px;
		color: #2f2f2f;
		outline: none;

		@include mobile {
			padding: 12px 18px;
		}
		
		&:focus {
			border: 1px solid #350d50;
		}
	}

	&__btn {
      background: #35736e;
	  border: 1px solid #c4c4c4;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  width: 50px;
	  flex: 1 0 50px;
	  border-radius: 10px;
	  transition: 0.3s background;

	  span {
		display: none;
	  }

	  @include mobile {
		padding: 12px 18px;
		span {
			display: block;
			color: #fff;
		}

		svg {
			display: none;
		}
	  }

	  &:hover {
		background: darken(#35736e, 10);
	  }

	 
	}
}
