.section {
  &__help {
    @include mobile {
     padding: 0 0 50px 0;
    }
    padding: 70px 0 100px 0;
    position: relative;
    .help__svg {
      position: absolute;
      left: 5%;
      top: 0;
      @include mobile {
        top: unset;
        bottom: 0;
      }
      
    }
  }

}

.help {
  display: flex;
  justify-content: space-between;
  gap: 55px;
  @include mini-desktop {
    flex-wrap: wrap-reverse;
  }
  &__item {
   
    &.left {
      &__item {
        position: relative;
        display: flex;
        align-items: flex-start;
        gap: 30px;
        flex: 0 1 630px;
        @include mini-desktop {
          display: flex;
          flex: 0 1 100%;
          flex-wrap: wrap;
        }
      }
    }

    &.right {
      &__item {
        @include mini-desktop {
          flex: 0 1 100%;
        }
      }
    }
  }

  &__img-1 {
    @include mobile {
      max-width: 90%;
      width: 100%;
      height: auto;
    }
  }

  &__img-2 {
    @include mobile {
     display: none;
    }
  }


  &__img-3 {
    position: absolute;
    bottom: -30px;
    right: 0;

    @include mobile {
      max-width: 40%;
      width: 100%;
      height: auto;
      top: 5%;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    margin-top: 30px;
    @include mobile {
     margin-top: 10px;
     gap: 15px;
    }

    li {
      position: relative;
      padding-left: 22px;
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;
      color: #6a6a6a;
      flex: 0 1 196px;
      @include mobile {
       flex: 0 1 100%;
      }
      &:after {
       position: absolute;
       content: "";
       border: 2px solid #3b5d50;
       height: 8px;
       width: 8px;
       left: 0;
       top: 6px;
       border-radius: 100%;
      }
    }
  }
}
